<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="data"
          ref="formRef"
          label-width="130px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学生姓名" prop="stuId" required>
                <el-select
                  v-model="data.stuId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.stuName"
                    :value="item.stuId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="学号" required prop="stuId">
                <el-input
                  v-model="data.stuId"
                  placeholder="请输入教师工号"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="性别" required prop="stuGender">
                  <el-select v-model="data.stuGender">
                      <el-option
                          label="男"
                          value="0">
                      </el-option>
                      <el-option
                          label="女"
                          value="1">
                      </el-option>
                  </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="民族" required prop="stuNation">
                <el-input
                  v-model="data.stuNation"
                  placeholder="请输入民族"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

            <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="入学日期" required prop="entranceDate">
            <el-input
              v-model="data.entranceDate"
              placeholder="请输入教研室id"
            ></el-input>
          </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="类别" required prop="stuType">
                <el-input
                  v-model="data.stuType"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="班级" required prop="classId">
                <el-select
                  v-model="data.classId"
                  placeholder="请输入内容"
                  filterable=""
                >
                  <el-option
                    v-for="item in classOptions"
                    :key="item.id"
                    :label="item.className"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="所属专业" required prop="majorId">
                <el-select
                  v-model="data.majorId"
                  placeholder="请输入内容"
                  filterable=""
                >
                  <el-option
                    v-for="item in majorOptions"
                    :key="item.id"
                    :label="item.majorName"
                    :value="item.majorId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="照片" required prop="stuPic">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  :headers="config"
                  :on-success="saveUrl"
                  :before-upload="beforeUpload"
                  accept=".jsp,.png"
                >
                  <el-button size="small" type="primary">点击上传</el-button>
                  <span slot="tip" class="el-upload_Tip">
                         只能上传jpg/png文件
                  </span>
                </el-upload>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="出生日期" required prop="stuBirthday">
                <el-input
                  v-model="data.stuBirthday"
                  placeholder="请输入职务"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="身份证号" required prop="stuIdCard">
                <el-input
                  v-model="data.stuIdCard"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="家庭出身" required prop="familyOrigin">
                <el-input
                  v-model="data.familyOrigin"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="政治面貌" required prop="stuPolitics">
                <el-input
                  v-model="data.stuPolitics"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="婚否" required prop="isMarried">
                  <el-select v-model="data.isMarried">
                      <el-option
                          label="是"
                          value="0">
                      </el-option>
                      <el-option
                          label="否"
                          value="1">
                      </el-option>
                  </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="高中毕业院校" required prop="highSchool">
                <el-input
                  v-model="data.highSchool"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="邮编" required prop="postCode">
                <el-input
                  v-model="data.postCode"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="家庭联系电话" required prop="homePhone">
                <el-input
                  v-model="data.homePhone"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="家庭永久通讯处" required prop="homeAddress">
                <el-input
                  v-model="data.homeAddress"
                  placeholder="请输入"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>


          <span class="dialog-footer">
                <el-button type="primary" @click="submitForm">保存</el-button>
            </span>


          
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    config() {
      return { Token: this.token }
    }
  },
  data() {
    return {
      data: this.$route.query.data || {},
      accessory: [],
      Options: [],
      Form: {},
      classOptions: [],
      majorOptions: [],
      authForm: [],
      fileList: [],
      token: window.sessionStorage.getItem('token'),
      photoView: '',
      dialogVisible: false,
      List: [],
      isstu: undefined,
      rules: {
        stuDepartId: [
          { required: true, message: '部门不能为空', trigger: 'blur' }
        ],
        stuMjaorId: [
          { required: true, message: '所属专业不能为空', trigger: 'blur' }
        ],
        stuId: [
          { required: true, message: '教教师工号不能为空', trigger: 'blur' }
        ],
        stuName: [
          { required: true, message: '教师名不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getTreeselect()
  },
  computed: {
    config() {
      return { Token: this.token }
    }
  },
  mounted(){
      this.$route.query.data.classId=this.$route.query.data.classId+''
  },
  methods: {
    photo(row) {
      this.dialogVisible = true
      this.photoView = row.stuPic
    },
    beforeUpload(file) {
      console.log(file.type)

      const isjpg = file.type === 'image/png' || file.type === 'image/jpeg'
      if (!isjpg) {
        this.$message.error('请上传 JPG或者PNG 格式!')
      }
      return isjpg
    },
    async saveUrl(response) {
      this.Form.stuPic = response.data.url
      console.log(this.Form.stuPic)
    },
    delRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/copyRightAuthorInfo/removeByIdCopyRightAuthorInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
        const { data: resstu } = await this.$http.post(
        '/studentInfo/queryStudentInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: res } = await this.$http.post(
        '/majorInfo/queryMajorInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: resClass } = await this.$http.post(
        '/classInfo/queryClassInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const arr = res.data.dataList.records
      this.Options = resstu.data.dataList.records
      this.majorOptions = this.unique(arr)
      this.classOptions = resClass.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
      // console.log(this.classOptions)
      // console.log(this.majorOptions) 
    },
    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },

    submitForm(){
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/studentInfo/saveOrUpdateStudentInfo',
            this.data
          )
          if (res.code != 20000) return this.$message.error(res.message)
          else{
            return this.$message.success(res.message)
            this.$router.go(-1)

          }
        }
      })
        
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save {
  display: block;
  margin: 20px auto;
  margin-bottom: 0;
  padding: 18px 30px;
}
.add {
  margin-left: 10px;
}
.dialog-footer{
    margin-bottom: 10px;
    float: right;
}
</style>
